import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import {
    DataTable, TableContainer, Table, TableToolbar, TableBatchActions, TableBatchAction,
    TableToolbarContent, TableToolbarSearch, TableToolbarMenu, TableToolbarAction, Button,
    TableHead, TableRow, TableSelectAll, TableHeader, TableBody, TableSelectRow, TableCell,
    OverflowMenu, OverflowMenuItem
} from "carbon-components-react";
import { EventAPI } from "../../commons/EventAPI";
import SrsIcon from "./Icon.js";
import { Settings } from "@carbon/icons-react";

const dispatchedEvents = ["ABOUT#clicked"];
/*
    Example: <srs-datatable headers=["Name", "Email", "Phone"] rows=[["John Doe", "john.doe@example.com", "(123) 456-7890"]]/>
*/
const SrsDataTable = function (props, ref) {
    const headers = props.headers;
    const [rows, setRows] = useState(props.rows || []);
    const [selectedRows, setSelectedRows] = useState([]);

    const [sortBy, setSortBy] = useState("");
    const [sortDirection, setSortDirection] = useState("");
    const [filter, setFilter] = useState("");

    const handleSort = (event) => {
        const { key, direction, inlineSort, dispatchEvent } = event;

        setSortBy(key);
        setSortDirection(direction);
        if (inlineSort) {
            rows = rows.sort((a, b) => direction == "ASC" ? a[key].localeCompare(b[key]) : b[key].localeCompare(a[key]));
            setRows(rows);
        } else {
            dispatchEvent(EventAPI.DATATABLE_SORT_REQUESTED, { payload: { sortBy: key, sortDirection: direction } });
        }
    };

    // Event listener for row clicks
    const onRowClick = (evt) => {
        // Find the clicked row element
        let target = evt.target;
        while (target.tagName !== 'TR') {
            target = target.parentNode;
            if (!target) {
                return;
            }
        }
        // Ignore clicks on the table header row
        if (target.parentNode.tagName === 'THEAD') {
            return;
        }
        // Select the clicked row
        this.selectedIndex = target.rowIndex - 1;
        props.dispatchEvent(EventAPI.DATATABLE_ROW_CLICKED, { detail: { index: this.selectedIndex } });
    }

    const handleSelectRow = (row) => {
        const rowIndex = selectedRows.findIndex((r) => r.id === row.id);
        if (rowIndex >= 0) {
            setSelectedRows(selectedRows.filter((r) => r.id !== row.id));
        } else {
            setSelectedRows([...selectedRows, row]);
        }
    };

    const handleRemoveSelectedRows = () => {
        // remove selected rows from the table
    };

    const handleFilterChange = (event) => {
        setFilter(event.target.value);
    };

    const onInputChange = (evt) => {
        console.log(evt.target.value);
    }

    return (
        <>
            <TableContainer title={props.list_title} description={props.list_subtitle}>
                <TableToolbar>
                    <TableToolbarContent>
                        <TableToolbarSearch onChange={onInputChange} />
                        <TableToolbarMenu>
                            <TableToolbarAction onClick={handleRemoveSelectedRows} isDelete disabled={!selectedRows || selectedRows.length == 0}>
                                Delete Selected
                            </TableToolbarAction>
                            <TableToolbarAction onClick={handleRemoveSelectedRows} disabled={!selectedRows || selectedRows.length == 0}>
                                Export Selected
                            </TableToolbarAction>
                        </TableToolbarMenu>
                    </TableToolbarContent>
                </TableToolbar>
            {/* <div style={{ display: "flex", flexDirection: "row", verticalAlign: "center" }}>
                <div style={{ display: "flex", flexDirection: "column", width: "80%", padding: "10px 10px 0px 10px" }}>
                    <p className="cds--file--label">{props.list_title}</p>
                    <p className="cds--label-description">{props.list_subtitle}</p>
                </div>
                <OverflowMenu renderIcon={Settings} ariaLabel="overflow-menu" size="lg">
                    <OverflowMenuItem itemText="Delete Selected" isDelete onClick={handleRemoveSelectedRows} disabled={!selectedRows || selectedRows.length == 0} />
                    <OverflowMenuItem itemText="Export Selected" onClick={handleRemoveSelectedRows} disabled={!selectedRows || selectedRows.length == 0} />
                </OverflowMenu>
            </div> */}
                <Table size="lg" useZebraStyles={true}
                    filter={
                        <label>
                            Filter by name:
                            <input type="text" value={filter} onChange={handleFilterChange} />
                        </label>
                    }>
                    <TableHead>
                        <TableRow>
                            <TableHeader id="OpenRecord" key="OpenRecord"></TableHeader>
                            {headers.map((header) => {
                                return typeof header == "object" ? (<TableHeader
                                    {...header}
                                    isSortable={header.sortable}
                                    key={header.name}
                                    sortDirection={sortBy === header.name ? sortDirection : null}
                                    onClick={() => handleSort({ key: header.name, direction: sortDirection === "ASC" ? "DESC" : "ASC", inlineSort: header.inline_sort, dispatchEvent: props.dispatchEvent })}>
                                    {header.label}
                                </TableHeader>) : <TableHeader id={header} key={header}>{header}</TableHeader>
                            })
                            }
                            <TableHeader id="Actions" key="Actions">Row Actions</TableHeader>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow key={row.id}>
                                <TableCell>
                                    <Button
                                        kind="ghost"
                                        hasIconOnly
                                        renderIcon={() => {
                                            return (<SrsIcon type="fontawesome" name="book-open" size={20} color={props.theme == "white" || props.theme == "g10" ? "black" : "white"} />);
                                        }}
                                        iconDescription="Open Record"
                                        onClick={() => onRowClick(row)}
                                    />
                                </TableCell>
                                {Object.keys(row)
                                    .filter((key) => key !== 'id')
                                    .map((key) => {
                                        return <TableCell key={key}>{row[key]}</TableCell>;
                                    })}
                                <TableCell>
                                    <div style={{ display: "flex", flexDirection: "row" }}>
                                        <Button
                                            kind="ghost"
                                            hasIconOnly
                                            renderIcon={() => {
                                                return (<SrsIcon type="carbon" name={selectedRows.some((r) => r.id === row.id) ? "CircleFilled" : "CircleStroke"} size={20} color={selectedRows.some((r) => r.id === row.id) ? "red" : "white"} />);
                                            }}
                                            iconDescription={selectedRows.some((r) => r.id === row.id) ? "Selected" : "Click to select"}
                                            onClick={() => handleSelectRow(row)}
                                        />
                                        <Button
                                            kind="ghost"
                                            hasIconOnly
                                            renderIcon={() => {
                                                return (<SrsIcon elementid={row.id} type="fontawesome" name="book-open" size={20} color={props.theme == "white" || props.theme == "g10" ? "black" : "white"} />);
                                            }}
                                            iconDescription="Open Record"
                                            onClick={() => onRowClick(row)}
                                        />
                                        <div style={{ verticalAlign: "center" }}>
                                            <OverflowMenu ariaLabel="overflow-menu" size="lg">
                                                <OverflowMenuItem itemText="Show Preview" />
                                                <OverflowMenuItem itemText="Delete Record" isDelete />
                                                <OverflowMenuItem itemText="Duplicate Record" disabled />
                                            </OverflowMenu>
                                        </div>
                                    </div>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    );
}

const styles = `
    
`
const props = [{
    name: "list_title",
    defaultValue: "List",
    type: "string"
}, {
    name: "list_subtitle",
    defaultValue: "Below are the rows in the list.",
    type: "string"
}, {
    name: "rows",
    defaultValue: [],
    type: "object"
}, {
    name: "headers",
    defaultValue: [],
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsDataTable), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };